import axios from 'axios'

axios.defaults.timeout = (process.env.base === 'dev') ? 20000 : 10000;
axios.defaults.headers.get['Content-Type'] = 'application/json';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';


// api
const http = axios.create();
http.defaults.baseURL = 'https://api.order.nctu.me/api/v1/';
// http.defaults.baseURL = 'http://localhost:8000/api/v1/';

http.setToken = (token) => {
  if (token) http.defaults.headers.common['Authorization'] = 'Bearer ' + token;
}

http.request = async (method = 'get', url = '', options = {}) => {
  try {
    let res = (method !== 'get') ? await http[method](url, options) : await http[method](url, { params: options });

    return res.data;
  } catch (error) {
    return error.response.data;
  }
};

export default http;