import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: {
      isLogined: false,
      id: 0,
      phone: '',
      token: ''
    }
  },
  mutations: {
    setUser (state, user) {
      for (let key in user) {
        if (key in state.user) state.user[key] = user[key];
      }
    }
  },
  actions: {
    actionSetUser (context, user) {
      context.commit('setUser', user);
    },
    actionSetUserInit (context) {
      context.commit('setUser', {
        isLogined: false,
        id: 0,
        phone: '',
        token: ''
      });
    }
  },
  modules: {
  }
})
