import http from '@/utils/http'

let api = {};

api.setToken = (token) => {
  http.setToken(token);
}

api.search = (type, value, lng = 0, lat = 0) => {
  return http.request('get', `maskStore/${type}/${encodeURIComponent(value)}/${lng}/${lat}`);
}

api.location = (lng = 0, lat = 0) => {
  return http.request('get', `maskStore/location/${lng}/${lat}`);
}

api.register = ({phone, odd_or_even = 0}) => {
  return http.request('post', `maskUser/register`, { phone: phone, odd_or_even: odd_or_even });
}

api.login = (phone) => {
  return http.request('post', `maskUser/login`, { phone: phone });
}

api.updateFCMToken = (token) => {
  return http.request('put', `maskUser/updateFCMToken`, { token: token });
}

api.orderStore = (id) => {
  return http.request('post', `maskUser/order/store/${id}`);
}

api.deleteStore = (id) => {
  return http.request('delete', `maskUser/order/store/${id}`);
}

api.userStores = (lng = 0, lat = 0) => {
  return http.request('get', `maskUser/stores/${lng}/${lat}`);
}

export default api;