<template>
  <div id="app">
    <router-view/>

    <footer class="bg-light py-3">
      <div class="container text-center">copyright © Royalmar. All Rights Reserved.</div>
    </footer>
  </div>
</template>

<script>
import storage from '@/utils/storage'

export default {
  beforeMount() {
    let user = storage.get('user');
    if (user) {
      this.$store.dispatch('actionSetUser', user);

      this.$api.setToken(user.token);
    }
  }
}
</script>

